  align-items: center;
  transition: opacity .4s, visibility 0s .4s;
  display: flex;
  position: fixed;
  inset: 0;
}

.game-complete.show {
  opacity: 1;
  visibility: visible;
  transition: opacity .4s, visibility;
}

.game-complete.show .complete-content {
  opacity: 1;
  transform: scale(1)translateY(0);
}

