  bottom: var(--space-4);
  z-index: 1000;
  box-shadow: var(--shadow-lg);
  animation: toast-in var(--transition-base) ease-out, toast-out var(--transition-base) ease-in 1s forwards;
  font-weight: 500;
  position: fixed;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%);
}

@keyframes toast-in {
  0% {
    opacity: 0;
    transform: translate(-50%, 100%);
  }

  100% {
    opacity: 1;
    transform: translate(-50%);
  }
}

@keyframes toast-out {
